import { Observable } from 'rxjs/Rx';
import { Injectable, OnInit } from '@angular/core';
import { ReplaySubject } from 'rxjs/ReplaySubject';
import { Guess } from './models/guess.model';

@Injectable()
export class WordingService {

  wordList: string[];
  guess: ReplaySubject<Guess> = new ReplaySubject(1);

  constructor() {
    let wordab:string[]=["word","Title","define","duck",
    "apple","fan","clock","chair"
 ,"table","bed","bold","book",
 "board","banana","hair",
 "brush","laptop","base","computer"
 ,"mouse","mobile","light","wall","cupboard",
 "flower","garden","human","switch","charger",
 "cup","glass","phone" ];
 
    let ab=Observable.of(wordab).subscribe(words => {
      this.wordList = words;
      this.pickAWord();
    });
  }

  public pickAWord() {
    const index = Math.floor(this.wordList.length * Math.random());
    const currentWord = this.wordList[index];
    this.guess.next(
      new Guess(
        currentWord.length,
        currentWord,
        this.scramble(currentWord)
      ));
  }

  private scramble(word: string) {
    function randomSort(a, b) {
      return Math.random() > .5 ? -1 : 1;
    }
    const scrambled = word.split('').sort(randomSort).join('').toUpperCase();
    return word.toUpperCase() !== scrambled ? scrambled : this.scramble(word);
  }
}
