import { Component, OnInit, EventEmitter, Output, Input, OnDestroy } from '@angular/core';
import { KidService } from '../word.service';
import { KidGuess } from '../models/Kidguess.model';

@Component({
  selector: 'kid-guess',
  templateUrl: './word.component.html',
  styleUrls: ['./word.component.scss', '../kid.component.scss']
})
export class KidWordComponent implements OnInit, OnDestroy {
  guessWord: KidGuess;
  nbTyped: number = 0;
  guessValue: string = '';
  guessSubscription;
  @Input()
  finished: boolean;

  @Output()
  foundAWord = new EventEmitter<KidGuess>();

  constructor(private ws: KidService) {}

  ngOnInit(): void {
    this.guessSubscription = this.ws.guess.subscribe(guess => {
      this.guessWord = guess;
    });
 

  }


  evaluateGuess(): void {
    this.nbTyped++;
    if (this.guessValue.toUpperCase() === this.solution().toUpperCase()) {
      this.foundAWord.emit(new KidGuess(this.nbTyped, this.solution()));
      this.nbTyped = 0;
      this.guessValue = '';
      this.ws.pickAWord();
    }
  }

  solution() {
    return this.guessWord.solution;
  }

  ngOnDestroy(): void {
    if (this.guessSubscription) {
      this.guessSubscription.unsubscribe();
    }
  }
}
