
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './components/login/login.component';
import { HomeComponent } from './components/home/home.component';
import { CustomersComponent } from './components/customers/customers.component';
import { SettingsComponent } from './components/settings/settings.component';
import { AboutComponent } from './components/about/about.component';
import { NotFoundComponent } from './components/not-found/not-found.component';
import { AuthService } from './services/auth.service';
import { AuthGuard } from './services/auth-guard.service';
import { TypingTestComponent } from './components/TypingTest/typingtest';

import { practicingsystemComponent } from './components/practicingsystem/practicingsystem.component';
import { testkeyboradComponent } from './components/testkeyborad/testkeyborad.component';
import { SidenavComponent } from './components/sidenav/sidenav.component';
import { hangmansComponent } from './hang-man/hangmans.component';
import { GameComponent } from './components/wordgame/game/game.component';
import { wordgameComponent } from './components/wordgame/wordgame.component';
import { KidGameComponent } from './components/learnkid/game/game.component';
import { KidComponent } from './components/learnkid/kid.component';
import { KidHighscoreComponent } from './components/learnkid/highscore/highscore.component';
import { TodoDemoComponent } from './components/controls/todo-demo.component';
import { typefallComponent } from './components/typefall/typefallcomponent';
export const routes: Routes = [
  {
    path: '', component: HomeComponent,
    // canActivate: [AuthGuard],
    data: { title: 'Home' }
  },
  { path: '', component: SidenavComponent },
  { path: 'login', component: LoginComponent, data: { title: 'Login' } },
  { path: 'customers', component: CustomersComponent, data: { title: 'Customers' } },
  { path: 'testkeyboard', component: testkeyboradComponent, data: { title: 'testkeyborad' } },
  { path: 'todo', component: TodoDemoComponent, data: { title: 'Todo' } },
  { path: 'settings', component: SettingsComponent, data: { title: 'Settings' } },
  { path: 'practicing', component: practicingsystemComponent, data: { title: 'Practicing' } },
  { path: 'typingtest', component: TypingTestComponent, data: { title: 'TypingTest' } },
  {
    path: 'learnkid', component: KidComponent, data: { title: 'LernKid' },
    children: [
      {
        path: 'kid',
        component: KidGameComponent
      },
      { path: 'highscores', component: KidGameComponent },
    ]
  },
  { path: 'hitbubble', component: hangmansComponent, data: { title: 'Bubble Falling' } },
  { path: 'typefall', component: typefallComponent, data: { title: 'Type Fall' } },

  {
    path: 'wordfinder', component: wordgameComponent,
    children: [
      {
        path: 'play',
        component: GameComponent
      },
      { path: 'highscores', component: GameComponent },

    ]
  },
  { path: 'highscores', component: GameComponent },
  { path: 'about', component: AboutComponent, data: { title: 'About Us' } },
  { path: 'home', redirectTo: '/', pathMatch: 'full' },
  { path: '**', component: NotFoundComponent, data: { title: 'Page Not Found' } }
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [AuthService, AuthGuard]
})
export class AppRoutingModule { }
